<template>
    <section class="main">
        <loader v-if="!configLoaded" />
        <h1 v-if="configLoaded && isDeviceLinked" class="intro">
            <html-with-links tag="span" class="intro__inner" :html="$t('INSTALL_APPS_WITH_FREE_DEV_ACCOUNT')" />
        </h1>
        <div v-if="configLoaded" class="row">
            <div class="col col_1">
                <template v-if="isDeviceLinked">

                    <table class="table">
                        <template v-if="!config.plus_account?.appleid
                            || config.plus_account?.is_yours === 0
                            || configuring
                            || showConfigure">
                            <tr>
                                <td class="cell">
                                    Apple account {{ $t('OF_YOUR_OWN_DEV_ACCOUNT') }}<br />
                                    <a href="https://www.youtube.com/watch?v=-ShhaoaLWyE" target="_blank">{{
                                        $t('VIDEO_TUTORIAL') }}</a>
                                </td>
                                <td class="td">
                                    <input class="input" v-model="appleid" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                    {{ $t('PASSWORD') }}
                                </td>
                                <td class="td">
                                    <input class="input" type="password" v-model="password" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                </td>
                                <td class="td">
                                    <html-with-links tag="span" class="hint"
                                        :html="$t('PLUS_PRIVACY_HINT', { privacy_href: '/info/privacy' })" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell">
                                    {{ $t('SEND_SMS_TO_VERIFY_ACCOUNT') }}
                                </td>
                                <td class="td">
                                    <yes-no v-model="isSMSAuth"
                                        :options="[{ label: $t('YES'), value: 1 }, { label: $t('NO'), value: 0 }]"
                                        fit-content />
                                    <html-with-links tag="span" class="hint hint_mt" :html="$t('SMS_AUTH_HINT')" />
                                </td>
                            </tr>
                            <tr>
                                <td class="cell"></td>
                                <td class="td">
                                    <btn class="configure-button" type="primary" :loading="configuring"
                                        :disabled="!appleid || !password" @click.native="configurePlusAccount">
                                        {{ $t('CONFIGURE_PLUS_ACCOUNT') }}
                                    </btn>
                                </td>
                            </tr>
                        </template>
                        <tr v-if="config.plus_account?.appleid && config.plus_account?.is_yours !== 0">
                            <td class="cell">
                                Apple account<br />
                                <a href="https://www.youtube.com/watch?v=-ShhaoaLWyE" target="_blank">{{
                                    $t('VIDEO_TUTORIAL') }}</a>
                            </td>
                            <td class="td">
                                {{ config.plus_account?.appleid }}
                                <template v-if="!showConfigure">
                                    <br />
                                    <btn class="configure-button" type="primary" @click.native="reconfigureAccount">
                                        {{ $t('RECONFIGURE_YOUR_ACCOUNT') }}
                                    </btn>
                                </template>
                            </td>
                        </tr>
                        <tr v-if="!config.plus_account?.shared_by && config.plus_account?.appleid">
                            <td class="cell"></td>
                            <td class="td">
                                <btn class="delete-account-button" type="negative" :loading="deletingAccount"
                                    @click.native="deleteAccount">
                                    {{ $t('DELETE_PLUS_DEV_ACCOUNT') }}
                                </btn>
                            </td>
                        </tr>
                        <tr v-if="config.plus_account?.shared_by">
                            <td class="cell">
                                {{ $t('SHARED_WITH_YOU_BY') }}
                            </td>
                            <td class=td>{{ config.plus_account?.shared_by }}</td>
                        </tr>
                        <tr v-if="config.plus_account?.status_translated">
                            <td class="cell">
                                {{ $t('STATUS') }}
                            </td>
                            <td class="td">
                                <span v-html="config.plus_account?.status_translated"></span>
                                <template v-if="config.plus_account?.status === 'otp_required' && !configuring">
                                    <br />
                                    <btn class="configure-button" type="primary" :loading="sendingOtp"
                                        @click.native="sendOtp">
                                        {{ $t('ENTER_OTP_SHORT') }}
                                    </btn>
                                </template>
                                <template v-if="config.plus_account?.status === 'ok'
                                    && !config.plus_account?.shared_by">
                                    <br />
                                    <btn class="configure-button" type="primary" @click.native="downloadArchive">
                                        {{ $t('DOWNLOAD_ARCHIVE_FOR_DEVELOPMENT') }}
                                    </btn>
                                </template>
                            </td>
                        </tr>
                        <tr v-if="config.plus_account?.appleid && !config.plus_account?.shared_by">
                            <td class="cell">
                                {{ $t('SHARED_WITH_FRIENDS') }}<br />
                                <a href="https://www.youtube.com/watch?v=v0u7c4z59-s" target="_blank">{{
                                    $t('VIDEO_TUTORIAL') }}</a>
                            </td>
                            <td class="td">
                                <div class="shared-row" v-for="account in config.plus_account?.shared_with"
                                    :key="account.email">
                                    {{ account.email }}
                                    <span style="white-space: nowrap">
                                        ({{ $t('DEVICES_COUNT', { n: account.devices_amount }) }})
                                    </span>&nbsp;&nbsp;<btn type="negative" class="unsh-button"
                                        :loading="unsharing.includes(account.email)"
                                        @click.native="unshare(account.email)">{{ $t('Remove') }}</btn>
                                </div>
                                <btn type="primary" class="sh-action" @click.native="share" :loading="sharing">{{
                                    $t('SHARE_WITH_FRIEND') }}
                                </btn>
                            </td>
                        </tr>
                        <tr v-if="config.plus_account && config.plus_account?.is_yours !== 0">
                            <td class="cell">
                                {{ $t('P2P_TRADING') }}
                                <br />
                                <router-link to="/my/p2p-certificates">{{ $t('LEARN_MORE') }}</router-link>
                            </td>
                            <td class="td">
                                <template v-if="config.plus_account?.is_p2p">
                                    {{ $t('ENABLED') }} <btn type="primary"
                                        @click.native="$router.push('/my/p2p-certificates')">{{ $t('VIEW_STATUS') }}
                                    </btn>
                                </template>
                                <template v-else>
                                    {{ $t('DISABLED') }} <btn type="primary"
                                        @click.native="$router.push('/my/p2p-certificates')">{{ $t('ENABLE') }}
                                    </btn>
                                </template>
                            </td>
                        </tr>
                        <tr v-if="config.plus_account?.is_yours === 1 && config.plus_account?.is_p2p === 0">
                            <td colspan="2">
                                <router-link to="/my/p2p-certificates" class="banner">
                                    <span class="banner__title">
                                        {{ $t('GET_N_CASHBACK', { amount: '100%' }) }}
                                    </span>
                                    <span class="banner__desc">
                                        {{ $t('LEARN_MORE') }}
                                    </span>
                                </router-link>
                            </td>
                        </tr>
                    </table>
                </template>
            </div>
            <aside class="sidebar col col_2">
