            </aside>
        </div>
    </section>
</template>

<script>
import Loader from '@/components/Loader';
import Btn from '@/components/Button.vue';
import YesNo from '@/components/YesNo.vue';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import { mapGetters } from 'vuex';

export default {
    components: {
        Loader,
        Btn,
        HtmlWithLinks,
        YesNo,
    },
    data() {
        return {
            loading: false,
            configuring: false,
            sendingOtp: false,
            showConfigure: false,
            deletingAccount: false,
            sharing: false,
            unsharing: [],
            appleid: '',
            password: '',
            isSMSAuth: 0,
            plusOptions: [],
        };
    },
    computed: {
        ...mapGetters(['hasPlus', 'hasActivePlus', 'hasCanceledPlus', 'devicesLoaded', 'isDeviceLinked']),
        title() {
            return this.$t('INSTALL_CONFIGURE_YOUR_FREE_DEV_ACCOUNT');
        },
        configLoaded() {
            return !!this.config.model || (this.devicesLoaded && !this.isDeviceLinked);
        },
        config() {
            return this.$store.getters.config;
        },
        status() {
            if (this.hasPlus) {
                return 'ACTIVATED';
            }

            return 'NOT_ACTIVATED';
        },
    },
    methods: {
        cancelSubscription() {
            this.$confirm(this.$t('PLUS_SUBSCRIPTION_CANCEL_CONFIRMATION'))
                .then(() => this.$api.cancelPlusSubscription())
                .then((response) => {
                    if (response) {
                        window.location.href = response;
                        return;
                    }

                    this.$notify({
                        type: 'error',
                        text: this.$t('SOMETHING_WENT_WRONG'),
                    });
                });
        },
        async configurePlusAccount() {
            if (!this.config.plus_account?.appleid) {
                try {
                    await this.$confirm(this.$t('PLUS_DEV_ACCOUNT_FIRST_CONFIGURE_CONFIRMATION'), { yesOrNo: true });
                } catch {
                    this.$router.push('/my/faq?accent=step-1#official');
                    return;
                }
            }

            this.configuring = true;
            const result = await this.$api.editPlusDevAccount({
                appleid: this.appleid,
                password: this.password,
                isSMSAuth: this.isSMSAuth,
                status: this.config.plus_account?.appleid ? 'new' : '',
                // eslint-disable-next-line camelcase
                sharedWithEmails: this.config.plus_account?.shared_with?.map(a => a.email) || [],
            })
                .catch(e => e);

            if (result instanceof Error) {
                this.configuring = false;
                return;
            }

            this.handleStatus(this.appleid)
                .finally(() => {
                    this.configuring = false;
                });
        },
        async handleStatus(appleid) {
            await this.$store.dispatch('getConfig');
            const { status } = this.config.plus_account;

            if (status === 'ok') {
                this.$notify({
                    type: 'success',
                    text: this.$t('PLUS_ACCOUNT_CONFIGURED'),
                });
                return true;
            }

            if (status === 'error') {
                this.$notify({
                    type: 'error',
                    text: this.config.plus_account.status_translated,
                });
                return false;
            }

            if (status === 'otp_required') {
                const otp = await this.$prompt(this.$t('ENTER_OTP'));
                await this.$api.editPlusDevAccount({
                    appleid,
                    otp,
                    sharedWithEmails: this.config.plus_account.shared_with.map(a => a.email),
                });
            }

            return new Promise((resolve) => {
                setTimeout(() => {
                    resolve(this.handleStatus(appleid));
                }, 1000);
            });
        },
        async unshare(email) {
            this.$confirm(this.$t('DEV_ACCOUNT_SHARE_REMOVAL_CONFIRMATION', { email }))
                .then(() => {
                    this.unsharing.push(email);
                    return this.$api.editPlusDevAccount({
                        appleid: this.config.plus_account.appleid,
                        status: 'new',
                        sharedWithEmails:
                            this.config.plus_account.shared_with.map(a => a.email)
                                .filter(e => e !== email),
                    });
                })
                .then(() => this.handleStatus(this.config.plus_account.appleid))
                .finally(() => {
                    const idx = this.unsharing.indexOf(email);
                    this.unsharing.splice(idx, 1);
                });
        },
        share() {
            this.$prompt(this.$t('DEV_ACCOUNT_SHARE_PROMPT'))
                .then((email) => {
                    this.sharing = true;
                    return this.$api.editPlusDevAccount({
                        appleid: this.config.plus_account.appleid,
                        status: 'new',
                        sharedWithEmails: [...this.config.plus_account.shared_with.map(a => a.email), email],
                    });
                })
                .then(() => this.handleStatus(this.config.plus_account.appleid))
                .finally(() => {
                    this.sharing = false;
                });
        },
        deleteAccount() {
            this.$confirm(this.$t('DELETE_PLUS_DEV_ACCOUNT_CONFIRMATION'))
                .then(() => {
                    this.deletingAccount = true;
                    return this.$api.deletePlusDevAccount();
                })
                .then(() => this.$store.dispatch('getConfig'))
                .finally(() => {
                    this.deletingAccount = false;
                });
        },
        sendOtp() {
            this.sendingOtp = true;
            this.handleStatus(this.config.plus_account.appleid)
                .finally(() => {
                    this.sendingOtp = false;
                });
        },
        downloadArchive() {
            this.$api.getPlusDevAccountArchive()
                .then((response) => {
                    if (response) {
                        window.location.href = response;
                        return;
                    }

                    this.$notify({
                        type: 'error',
                        text: this.$t('SOMETHING_WENT_WRONG'),
                    });
                });
        },
        reconfigureAccount() {
            if (this.config.plus_account?.appleid && this.config.plus_account.appleid.includes('@')) {
                this.appleid = this.config.plus_account.appleid;
            }
            this.showConfigure = true;
        },
    },
    mounted() {
        const el = this.$route.hash && document.querySelector(this.$route.hash);
        if (el) {
            el.scrollIntoView();
        }
    },
    created() {
        this.$api.getSideloadingOptions('plus').then((response) => {
            this.plusOptions = response;
        });

        // Check if account is in verifying status and start polling
        this.$store.dispatch('getConfig').then(() => {
            if (this.config.plus_account &&
                this.config.plus_account.status !== 'ok' &&
                this.config.plus_account.status !== 'error' &&
                this.config.plus_account.status !== 'otp_required') {
                // Start checking status if it's in a verifying state
                this.handleStatus(this.config.plus_account.appleid);
            }
        });
    },
};
</script>

